/**
 * wordpress dependencies
 */
import { isEmpty } from 'lodash';
import { useDispatch, useSelect } from '@wordpress/data';
import { useState } from '@wordpress/element';
import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';

/**
 * Custom Imports
 */
import template from './template';
import Placeholder from './placeholder';
import PatternModal from './pattern-modal';
import CustomBlockControls from './block-controls';

function Edit( props ) {
	const [ isSelectingPattern, setSelectingPattern ] = useState( false );
	const {
		attributes: { showPlaceholder },
		setAttributes,
		clientId,
	} = props;
	const blockProps = useBlockProps( {
		className: 'gutenberghub-tabs-container',
	} );
	const { replaceBlocks } = useDispatch( 'core/block-editor' );
	const block = useSelect( ( select ) =>
		select( 'core/block-editor' )?.getBlock( clientId )
	);

	const hasInnerBlocks = ! isEmpty( block?.innerBlocks );

	const onPatternSelect = ( pattern ) => {
		const patternContent = wp.blocks.parse( pattern.content );
		replaceBlocks( clientId, patternContent );
	};

	return (
		<>
			<div { ...blockProps }>
				{ ! showPlaceholder && (
					<InnerBlocks
						template={ template }
						renderAppender={
							hasInnerBlocks
								? InnerBlocks.DefaultBlockAppender
								: InnerBlocks.ButtonBlockAppender
						}
					/>
				) }
				{ showPlaceholder && (
					<Placeholder
						setSelectingPattern={ setSelectingPattern }
						setAttributes={ setAttributes }
					/>
				) }
			</div>
			{ isSelectingPattern && (
				<PatternModal
					onSelect={ onPatternSelect }
					onRequestClose={ () => setSelectingPattern( false ) }
				/>
			) }

			{ ! showPlaceholder && (
				<CustomBlockControls
					{ ...props }
					setSelectingPattern={ setSelectingPattern }
				/>
			) }
		</>
	);
}
export default Edit;
