/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { select } from '@wordpress/data';
import { Modal } from '@wordpress/components';
import { __experimentalBlockPatternsList as BlockPatternsList } from '@wordpress/block-editor';

function PatternModal( props ) {
	const patterns = select( 'core/block-editor' ).getPatternsByBlockTypes(
		'ghub/tabs-container'
	);

	return (
		<Modal
			isFullScreen
			title={ __( 'Choose a pattern' ) }
			onRequestClose={ props.onRequestClose }
			overlayClassName="block-library-query-pattern__selection-modal"
		>
			<br />
			<div className="block-library-query-pattern__selection-content">
				<BlockPatternsList
					blockPatterns={ patterns }
					shownPatterns={ patterns }
					onClickPattern={ props.onSelect }
				/>
			</div>
		</Modal>
	);
}

export default PatternModal;
