import React from 'react';
import { __ } from '@wordpress/i18n';
import { Placeholder, Button } from '@wordpress/components';

function CustomPlaceholder( props ) {
	const { setSelectingPattern, setAttributes } = props;

	return (
		<Placeholder
			className="gutenberghub-tabs-placeholder"
			label={ __( 'Tabs', 'gutenberghub-tabs' ) }
			instructions={ __(
				'Choose a template or start blank.',
				'gutenberghub-tabs'
			) }
		>
			<div className="gutenberghub-tabs-patterns">
				<Button
					variant="primary"
					onClick={ () => setSelectingPattern( true ) }
				>
					{ __( 'Choose', 'gutenberghub-tabs' ) }
				</Button>

				<Button
					variant="secondary"
					onClick={ () =>
						setAttributes( { showPlaceholder: false } )
					}
				>
					{ __( 'Start Blank', 'gutenberghub-tabs' ) }
				</Button>
			</div>
		</Placeholder>
	);
}

export default CustomPlaceholder;
