/**
 * wordpress dependencies
 */
import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';

function Save( props ) {
	const blockProps = useBlockProps.save( {
		className:
			'gutenberghub-tabs-container gutenberghub-tabs-frontend-container',
	} );

	return (
		<div { ...blockProps }>
			<InnerBlocks.Content />
		</div>
	);
}
export default Save;
