/**
 * wordpress dependencies
 */
import { head } from 'lodash';
import { __ } from '@wordpress/i18n';
import { BlockControls } from '@wordpress/block-editor';
import { dispatch, select } from '@wordpress/data';
import { ToolbarGroup, ToolbarButton } from '@wordpress/components';

import { getBlockTypeIds } from '../../utils/get-block-type-ids';

function CustomBlockControls( props ) {
	const { setSelectingPattern } = props;

	const insertTab = () => {
		const tabTemplate = [ [ 'ghub/tab-button', {}, [] ] ];
		const contentTemplate = [ [ 'ghub/tab-content', {}, [] ] ];

		const { insertBlocks } = dispatch( 'core/block-editor' );

		const tabsButtonsContainerIds = getBlockTypeIds(
			props.clientId,
			'ghub/tab-buttons-container'
		);
		const contentBlockIds = getBlockTypeIds(
			props.clientId,
			'ghub/tab-contents-container'
		);

		const contentBlockId = head( contentBlockIds );
		const contentBlock =
			select( 'core/block-editor' ).getBlock( contentBlockId );

		const tabsButtonsContainerId = head( tabsButtonsContainerIds );
		const tabsButtonsContainer = select( 'core/block-editor' ).getBlock(
			tabsButtonsContainerId
		);

		const tabCreatedBlock =
			wp.blocks.createBlocksFromInnerBlocksTemplate( tabTemplate );
		const contentCreatedBlock =
			wp.blocks.createBlocksFromInnerBlocksTemplate( contentTemplate );

		insertBlocks(
			tabCreatedBlock,
			tabsButtonsContainer?.innerBlocks?.length + 1,
			tabsButtonsContainerId,
			true
		);
		insertBlocks(
			contentCreatedBlock,
			contentBlock?.innerBlocks?.length + 1,
			contentBlockId,
			true
		);
	};

	return (
		<BlockControls>
			<ToolbarGroup>
				<ToolbarButton onClick={ insertTab }>
					{ __( 'Add Tab', 'gutenberghub-tabs' ) }
				</ToolbarButton>
			</ToolbarGroup>
			<ToolbarGroup>
				<ToolbarButton onClick={ () => setSelectingPattern( true ) }>
					{ __( 'Replace', 'gutenberghub-tabs' ) }
				</ToolbarButton>
			</ToolbarGroup>
		</BlockControls>
	);
}
export default CustomBlockControls;
